import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import clsx from 'clsx';
import { MenuItem, makeStyles, InputAdornment } from '@material-ui/core';
import UploadButtons from '../../components/upload/uploadFile'

import AspectRatioIcon from '@material-ui/icons/AspectRatio';
import BatteryStdIcon from '@material-ui/icons/BatteryStd';
import CameraIcon from '@material-ui/icons/Camera';
import StorageIcon from '@material-ui/icons/Storage';
import MemoryIcon from '@material-ui/icons/Memory';
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';






const useStyles = makeStyles(theme => ({
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: 200,
    },
  },
}));

function DataProductForm(props) {

  const { loadData, loadBrand } = props;
  const classes = useStyles();
  const [model, setModel] = React.useState({ list: loadData && loadData.product ? loadBrand.brandResult.filter(item => item._id == loadData.product.brand)[0].models : [], value: loadData && loadData.product ? loadData.product.model : null });
  const [brand, setBrand] = React.useState({ list: loadBrand ? loadBrand.brandResult : [], value: loadData && loadData.product ? loadData.product.brand : null });
  const [values, setValues] = React.useState(loadData && loadData.product ? {
    screen: loadData.product.screen.$numberDecimal,
    camera: loadData.product.camera,
    storage: loadData.product.storage,
    memory: loadData.product.memory,
    battery: loadData.product.battery,
    dualSim: loadData.product.dualSim,
    price: loadData.product.price,
  } : {});


  const handleChangeBrand = event => {
    setBrand({ list: brand.list, value: event.target.value });
    setModel({ list: brand.list.filter(item => item._id == event.target.value)[0].models, value: null });
    props.UpdateChange('brand', event.target.value)
  };
  const handleChangeModel = event => {
    setModel({ list: model.list, value: event.target.value });
    props.UpdateChange('model', event.target.value)
  };

  const handleBlurField = event => {
    if (event.target.type == 'checkbox') {
      props.UpdateChange(event.target.id, event.target.checked)
    } else if (event.target.id == 'image') {
      props.UpdateChange(event.target.id, event.target.files[0])
    } else {
      props.UpdateChange(event.target.id, event.target.value)
    }
  };
  const handleChangeField = event => {
    if (event.target.type == 'checkbox') {
      setValues({ ...values, [event.target.id]: event.target.checked })
    } else if (event.target.id == 'image') {
      setValues({ ...values, [event.target.id]: event.target.files[0] })
    } else {
      setValues({ ...values, [event.target.id]: event.target.value })
    }
  };

  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
        General Information
      </Typography>
      <form enctype="multipart/form-data">
        <Grid container spacing={3}>
          <Grid item xs={12} sm={6}>
            <TextField
              fullWidth
              id="brand"
              select
              label="Brand"
              value={brand.value}
              onChange={handleChangeBrand}
              helperText="Please select your Brand"
            >
              {brand.list.map(option => (
                <MenuItem key={option._id} value={option._id}>
                  {option.name}
                </MenuItem>
              ))}
            </TextField>

          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              fullWidth
              id="model"
              select
              label="Model"
              value={model.value}
              onChange={handleChangeModel}
              helperText="Please select your model"
            >
              {model.list.map(option => (
                <MenuItem key={option._id} value={option._id}>
                  {option.name}
                </MenuItem>
              ))}
            </TextField>
          </Grid>
          <Grid item xs={12} sm={3}>
            <TextField
              fullWidth
              label="Screen"
              type="number"
              id="screen"
              className={clsx(classes.margin, classes.textField)}
              value={values.screen}
              onChange={handleChangeField}
              onBlur={handleBlurField}
              InputProps={{
                endAdornment: <InputAdornment position="end"><AspectRatioIcon /></InputAdornment>,
              }}
            />
          </Grid>
          <Grid item xs={12} sm={3}>
            <TextField
              fullWidth
              label="Camera"
              type="number"
              id="camera"
              value={values.camera}
              onChange={handleChangeField}
              onBlur={handleBlurField}
              className={clsx(classes.margin, classes.textField)}
              onBlur={handleBlurField}
              InputProps={{
                endAdornment: <InputAdornment position="end"><CameraIcon /></InputAdornment>,
              }}
            />
          </Grid>
          <Grid item xs={12} sm={3}>
            <TextField
              fullWidth
              label="Storage"
              type="number"
              id="storage"
              className={clsx(classes.margin, classes.textField)}
              value={values.storage}
              onChange={handleChangeField}
              onBlur={handleBlurField}
              InputProps={{
                endAdornment: <InputAdornment position="end"><StorageIcon /></InputAdornment>,
              }}
            />
          </Grid>
          <Grid item xs={12} sm={3}>
            <TextField
              fullWidth
              label="Memory"
              type="number"
              id="memory"
              className={clsx(classes.margin, classes.textField)}
              value={values.memory}
              onChange={handleChangeField}
              onBlur={handleBlurField}
              InputProps={{
                endAdornment: <InputAdornment position="end"><MemoryIcon /></InputAdornment>,
              }}
            />
          </Grid>
          <Grid item xs={12} sm={3}>
            <TextField
              fullWidth
              label="Battery"
              type="number"
              id="battery"
              className={clsx(classes.margin, classes.textField)}
              value={values.battery}
              onChange={handleChangeField}
              onBlur={handleBlurField}
              InputProps={{
                endAdornment: <InputAdornment position="end">
                  <BatteryStdIcon />
                </InputAdornment>,
              }}
            />
          </Grid>
          <Grid item xs={12} sm={3}>
            <TextField
              fullWidth
              label="Price"
              type="number"
              id="price"
              className={clsx(classes.margin, classes.textField)}
              value={values.price}
              onChange={handleChangeField}
              onBlur={handleBlurField}
              InputProps={{
                endAdornment: <InputAdornment position="end">
                  <AttachMoneyIcon />
                </InputAdornment>,
              }}
            />
          </Grid>
          <Grid item xs={12} sm={3}>
            <FormControlLabel
              control={
                <Checkbox onChange={handleBlurField}
                  checked={values.dualSim}
                  onChange={handleChangeField} color="secondary" id="dualSim" name="dualSim" />}
              label="Dual Sim"
            />
          </Grid>

          <Grid item xs={12} sm={9}>
            <UploadButtons handleChangeImage={handleBlurField} />
          </Grid>

        </Grid>
      </form>

    </React.Fragment>
  );
}

export default DataProductForm;