import React from 'react';
import AdminLayout from '../../layouts/AdminLayout';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Paper from '@material-ui/core/Paper';
import DataProductForm from './DataProductForm';
import Typography from '@material-ui/core/Typography';
import { connect } from 'react-redux';
import withStyles from '@material-ui/core/styles/withStyles';
import { productActions } from '../../store/actions/product.action';
import { withRouter } from 'react-router-dom';
import LoadingCircle from '../../components/Progress/loadingCircle';

const styles = theme => ({
	appBar: {
		position: 'relative'
	},
	layout: {
		width: 'auto',
		marginLeft: theme.spacing(2),
		marginRight: theme.spacing(2),
		[theme.breakpoints.up(600 + theme.spacing(2) * 2)]: {
			width: 600,
			marginLeft: 'auto',
			marginRight: 'auto'
		}
	},
	paper: {
		marginTop: theme.spacing(3),
		marginBottom: theme.spacing(3),
		padding: theme.spacing(2),
		[theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
			marginTop: theme.spacing(6),
			marginBottom: theme.spacing(6),
			padding: theme.spacing(3)
		}
	},
	stepper: {
		padding: theme.spacing(3, 0, 5)
	},
	buttons: {
		display: 'flex',
		justifyContent: 'flex-end'
	},
	button: {
		marginTop: theme.spacing(3),
		marginLeft: theme.spacing(1)
	}
});

function ProductForm(props) {
	const { classes, loadingData, loadingBrand } = props;
	const [state, setState] = React.useState({});
	const [sendingData, setSendingData] = React.useState(false);

	React.useEffect(() => {
		let editID = props.history.location.state;
		if (editID && editID.id) {
			props.handleLoadData(editID.id);
		} else {
			props.handleLoadData();
		}
	}, []);

	React.useEffect(() => {
		if (props.loadingCreateProd) {
			setSendingData(true);
		} else if (!props.loadingCreateProd && sendingData) {
			setSendingData(false);
			props.history.push({
				pathname: '/home'
			});
		}
	}, [props.loadingCreateProd]);

	const handleSave = () => {
		const formData = new FormData();
		Object.keys(state).forEach(element => {
			formData.append(element, state[element]);
		});
		if (props.loadData && props.loadData.product) {
			formData.append('_id', props.loadData.product._id);
			props.handleUpdateProduct(formData);
		} else {
			props.handleSaveProduct(formData);
		}
	};
	const UpdateChange = (name, value) => {
		setState(oldState => ({ ...oldState, [name]: value }));
	};
	return (
		<AdminLayout fullWith centred>
			<CssBaseline />
			<main className={classes.layout}>
				{loadingData || loadingBrand ? (
					<LoadingCircle generalCenter />
				) : (
					<Paper className={classes.paper}>
						<Typography component='h1' variant='h4' align='center'>
							Add Phone
						</Typography>
						<DataProductForm
							loadData={props.loadData}
							loadBrand={props.listBrand}
							UpdateChange={(name, value) => UpdateChange(name, value)}
						/>
						<div className={classes.buttons}>
							<Button
								variant='contained'
								color='primary'
								onClick={handleSave}
								className={classes.button}
							>
								{props.loadData && props.loadData.product ? 'Update' : 'Save'}
							</Button>
						</div>
					</Paper>
				)}
			</main>
		</AdminLayout>
	);
}

const mapStateToProps = ({ productReducer, brandReducer }) => {
	return {
		loadingCreateProd: productReducer.loadingCreateProd,
		createdSucc: productReducer.created,
		createdError: productReducer.error,

		loadingData: productReducer.loadingData,
		loadData: productReducer.loadData,
		errorData: productReducer.errorData,

		listBrand: brandReducer.listBrand,
		loadingBrand: brandReducer.loadingList
	};
};
const mapDispatchToProps = dispatch => {
	return {
		handleSaveProduct: data => {
			dispatch(productActions.CreateProduct(data));
		},
		handleUpdateProduct: data => {
			dispatch(productActions.UpdateProduct(data));
		},
		handleLoadData: data => {
			dispatch(productActions.LoadData(data));
		}
	};
};
export default withRouter(
	connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(ProductForm))
);
